@keyframes pulse {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.05);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}


/* Estilos generales */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
}

.container {
    max-width: 800px;
}

.roundedBox {
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Estilos de la evaluacin */
.evaluationBox {
    background-color: #ffffff;
    padding: 16px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.evaluationTitle {
    font-weight: bold;
    color: #333;
    margin-right: 10px; /* Espaciado entre el ttulo y el crculo de calificacin */
}

.gradeCircle {
    border-radius: 50%;
    text-align: center;
    line-height: 80px; /* Ajustar segn el tamao del crculo */
    width: 80px;
    height: 80px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.buttonHover:hover {
    background-color: #e2e2e2; /* Un color ms claro al pasar el cursor */
    transition: background-color 0.3s ease;
}

